<template>
    <div>
      <van-list
  v-model="loading"
  :finished="finished"
  finished-text="没有更多了"
  @load="onLoad"
>
  <articleItem v-for="item in list" :key="item.id" :item="item"></articleItem>
</van-list>

    </div>
  </template>

<script>
import articleItem from '@/components/article-item.vue'
import { getInterview } from '@/api/article'

export default {
  name: 'CollectPage',
  data () {
    return {
      page: 1,
      loading: false,
      finished: false,
      list: []
    }
  },
  components: {
    articleItem
  },
  methods: {
    async onLoad () {
      const res = await getInterview({ page: this.page, type: 2 })
      this.list.push(...res.data.data.rows)
      this.loading = false
      this.page++
      if (this.page > res.data.data.pageTotal) {
        this.finished = true
      }
    }
  }
}
</script>

